@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200;400;600;700;800;900&display=swap');

*, *::after, *::before{
  padding: 0;
  margin: 0;
  box-sizing: border-box ;
}

body{
  font-family: 'Poppins', sans-serif !important;
  background: #F2F2F2 !important;
}

.fw-600{
  font-weight: 600;
}

.fw-800{
  font-weight: 800;
}

.pcolor{
  color: #121C33;
}

.fs-12{
  font-size: 12px;
}

.round{
  border-radius: 32px !important;
}

.cursor{
  cursor: pointer;
}

.form-bg{
  background: #F5F5F5 !important;
}

.relative{
  position: relative;
}

.btn-bg{
  background: #121C33;
  border-radius: 12px;
}

.drop-width{
  position: absolute;
  right: 85px;
  top: -55px;
}

.nav-pills .nav-link.active{
  color: #121C33 !important;
  background-color: rgba(18, 28, 51, 0.05)  !important;
  border-radius: 8px;
  font-weight: 600;
}

.carde{
  border-radius: 16px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  background-color: #ffff;
}

.pHeight{
  width: 265px;
  height: 240px;
}

.cHeight{
  height: 200px;
  width: 250px;
}

.carde:hover{
  border: 2px solid black;
}

.carde-active{
  border: 2px solid black;
}

.img-full{
  width: 100%;
  height: 140px !important;
  border-radius: 15px 15px 0 0;
}

.add{
  background-color:#121C33;
  position: absolute;
  padding: 15px 20px;
  bottom: 25px;
  right: 0;
  z-index: 2;
}

.add-friend{
  width: 175px;
  background: rgba(18, 28, 51, 0.05)
}

.round-sm{
  border-radius: 16px;
  object-fit: cover;
}

.cwidth{
  width: calc(100% - 250px);
  position: relative;
  transition: 0.1s ease-in;
}

.show-cwidht{
  width: 100%;
  transition: 0.1s ease-in;
}

.preview{
  position: fixed;
  top: 25px;
  right: 10px;
  overflow-y: scroll;
  bottom: 20px;
  height: 100%;
  border-radius: 16px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  background-color: #ffff;
  width: 360px;
  transform: translate(105%);
  transition: 0.1s ease-in;
}

.show-preview {
  transform: translate(0);
}

.closeBar{
  background:rgba(18, 28, 51, 0.1);
  border-radius: 8px;
  padding: 5px 12px;
}

.preview::-webkit-scrollbar{
  width: 8px;
}

.preview::-webkit-scrollbar-track{
  background-color: #d1d2d3;
}

.preview::-webkit-scrollbar-thumb{
  background-color: #97989b;
}


@media screen and (max-width: 385px) {
  .preview{
    width: 300px;
  }
}

@media screen and (max-width: 500px) {
  .drop-width{
    position: relative;
    right: 65px;
    top: -5px;
  }
}